$breakpoint-tablet: 1000px;
$breakpoint-phone: 600px;


.divesites-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    .divesite-container{
        flex-basis: 33%;
        padding: 10px;

        .divesite-image{
            max-width: 70%;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            border-radius: 10px;
        }
        
        .divesite-options-container{
            margin-top: 10px;
        
            .divesite-option-title{
                font-weight: 800;
            }
        }

        .divesite-description-container{
            margin-top:20px;

            .divesite-description-title{
                
            }
        }

    }
}


@media (min-width: $breakpoint-phone) and (max-width: $breakpoint-tablet) {
    .divesites-container{
        .divesite-container{
            flex-basis: 50%;
        }
    }
}
@media (max-width: $breakpoint-phone) {
    .divesites-container{
        .divesite-container{
            flex-basis: 100%; 
        }
    }
}
